<template>
  <div class="show-process">
    <h1 class="display-1">–ü—Ä–æ—Ü–µ—Å—Å [{{pid}}]</h1>
    <div class="progress mt-3">
      <v-progress-linear
        height="18"
        rounded
        striped
        :color="color"
        :value="percent"
      ></v-progress-linear>
    </div>
    <v-card class="mt-3">
      <v-card-text>
        <p>–°—Ç–∞—Ç—É—Å –ø—Ä–æ—Ü–µ—Å—Å–∞: <span class="body">{{ pro—Åess_status ? pro—Åess_status.title : '' }}</span></p>
        <p class="text--secondary">{{ pro—Åess_status ? pro—Åess_status.description : '' }}</p>
        <p>–û–±—Ä–∞–±–æ—Ç–∞–Ω–æ –æ–ø–µ—Ä–∞—Ü–∏–π {{ proc.done }} –∏–∑ {{ proc.total }} </p>
      </v-card-text>
      <v-card-actions>
        <v-btn
          text
          v-if="proc.status > 1"
          color="primary"
          @click="$emit('onNewProcess')"
        >
          –ù–æ–≤—ã–π –ø—Ä–æ—Ü–µ—Å—Å
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
  export default {
    name: "ProcessPanel",
    props: {
      pid: {
        type: Number,
        required: true
      },
      proc: {
        type: Object,
        required: true
      }
    },
    computed: {
      color() {
        if (this.proc.status < 2) {
          return 'primary'
        } else if (this.proc.status === 2) {
          return 'success'
        } else {
          return 'error'
        }
      },
      percent() {
        return this.proc.done / this.proc.total * 100
      },
      pro—Åess_status() {
        switch (this.proc.status) {
          case 0:
            return {
              title: "–ù–û–í–´–ô",
              description: "–û–±—Ä–∞–±–æ—Ç–∫–∞ –ø—Ä–æ—Ü–µ—Å—Å–∞ –Ω–µ –∑–∞–ø—É—â–µ–Ω–∞"
            }
          case 1:
            return {
              title: "–û–ë–†–ê–ë–ê–¢–´–í–ê–ï–¢–°–Ø",
              description: "–í—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ –ø—Ä–æ—Ü–µ—Å—Å–∞"
            }
          case 2:
            return {
              title: "–û–ë–†–ê–ë–û–¢–ê–ù–û –£–°–ü–ï–®–ù–û",
              description: "–ü—Ä–æ—Ü–µ—Å—Å –æ–±—Ä–∞–±–æ—Ç–∞–ª—Å—è –±–µ–∑ –æ—à–∏–±–æ–∫"
            }
          case 3:
            return {
              title: "–û–ë–†–ê–ë–û–¢–ê–ù–û –° –û–®–ò–ë–ö–ê–ú–ò",
              description: "–ü—Ä–æ—Ü–µ—Å—Å –æ–±—Ä–∞–±–æ—Ç–∞–ª—Å—è –¥–æ –∫–æ–Ω—Ü–∞, –Ω–æ —Å –æ—à–∏–±–∫–∞–º–∏"
            }
          case 5:
            return {
              title: "–û–®–ò–ë–ö–ê",
              description: "–í –ø—Ä–æ—Ü–µ—Å—Å–µ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –ø—Ä–æ—Ü–µ—Å—Å–∞ –≤–æ–∑–Ω–∏–∫–ª–∞ –∫—Ä–∏—Ç–∏—á–µ—Å–∫–∞—è –æ—à–∏–±–∫–∞"
            }
          default:
            return {
              title: "–ù–û–í–´–ô",
              description: "–û–±—Ä–∞–±–æ—Ç–∫–∞ –ø—Ä–æ—Ü–µ—Å—Å–∞ –Ω–µ –∑–∞–ø—É—â–µ–Ω–∞"
            }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
